$(document).ready(function () {
    let i
    const overflow = document.html || document.getElementsByTagName(`html`)[0]
    const popup = document.getElementsByClassName(`popup`)
    const buttons = document.getElementsByClassName(`popup-button`)
    const span = document.getElementsByClassName(`close`)

    for (i = 0; i < buttons.length; i++) {
        buttons[i].onclick = function () {
            const id = this.getAttribute(`data-popup`)
            const popup = document.getElementById(id)
            popup.style.display = `block`
            overflow.style.overflowY = `hidden`
        }
    }

    for (i = 0; i < span.length; i++) {
        span[i].onclick = function () {
            const id = this.getAttribute(`data-popup`)
            const popup = document.getElementById(id)
            popup.style.display = `none`
            overflow.style.overflowY = `visible`
        }
    }

    window.onclick = function (event) {
        const isModal = (` ` + event.target.className + ` `).indexOf(` popup `) > -1
        if (isModal) {
            event.target.style.display = `none`
            overflow.style.overflowY = `visible`
        }
    }
})
